.sidebarWrapper{
    padding: 20px;
    
}
.sidebarList{
    padding: 0;
    margin: 0;
    list-style: none;
}
.sidebarListItem{
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}
.sidebarIcon{
    margin-right: 15px;
}
.sidebarButton{
    width: 150px;
    border: none;
    padding: 10px;
    border-radius: 5px;
    font-weight: 500;   
}
.sidebarHr{
    margin: 20px 0 ;
}
.sidebarFriendList{
    padding: 0;
    margin: 0;
    list-style: none;
    overflow-y: scroll;
    height: 300px;
    box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.65);
  border-radius: 10px;
padding: 10px;
}
.sidebarFriend{
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}
.sidebarFriendImg{
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-left: 10px;
}
.sidebarFriendName{
    margin-left: 10px;
}

::-webkit-scrollbar{
    width: 15px;
    height: 40px;
 }
 
 ::-webkit-scrollbar-thumb{
    background-color: #DBDBDB;
    border: 4px solid transparent;
    border-radius: 11px;
    background-clip: content-box;
 }
 
 ::-webkit-scrollbar * {
    background: transparent;
 }
 
 ::-webkit-scrollbar-thumb:vertical {
    height: 90px;
 }

