.rightBar {
  flex: 3.5;
  position: sticky;
  overflow-y: scroll;
    top: 50px;
    height: calc(100vh - 50px);
  border-radius: 10px;

}
.rightBarWraper {
  padding: 20px 20px 0 0;
}
.birthdayContainer {
  display: flex;
  align-items: center;
  box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.65);
  border-radius: 10px;
padding: 10px;
}
.birthdayText {
  font-weight: 300;
  margin-left: 5px;
  font-size: 15px;
  /* overflow: hidden; */
}
.rightBarImg {
  width: 100%;
  border-radius: 10px;
  margin: 20px 0;
  box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.65);

}
.rightBarTitle{
    margin-bottom: 20px;
}
.rightbarFriendList {
padding: 0;
margin: 0;
list-style: none;
height: 50vh;
overflow-y: scroll;
box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.65);
border-radius: 10px;
padding-left: 10px;
padding-top: 5px;
}

.rightbarFriend{
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}
.rightbarProfileImgContainr{
    margin-right: 10px;
    position: relative;
    z-index: -1;
}
.rightbarProfileImg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}

.rightbarOnline{
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: rgb(16, 235, 89);
    position: absolute;
    top: -2px;
    right: 0;
    border: 2px solid white;
}
.rightbarUserName{
margin-top: 5px;
margin-left: 2px;
}
.rightbarTitle{
  margin-bottom: 10px;
  font-size: 18px;
  font-weight: 500;
}
.rightbarInfo{
  margin-bottom: 30px;
}
.rightbarInfoItem{
  margin-bottom: 5px;
}
.rightbarInfoKey{
  font-weight: 500;
  margin-right: 10px;
  color:#555;
}
.rightbarInfoValue{
  font-size: 14px;
  font-weight: 500;
  color: rgb(122, 122, 122);
}
.rightbarFollowings{
  display: flex;
  flex-wrap: wrap;
  justify-content:start;
}
.rightbarFollowing{
display: flex;
flex-direction: column;
margin-bottom: 10px;
margin-right: 10px;
cursor: pointer;

}

.rightbarFollowingImg{
   width: 100px;
   height: 100px;
   object-fit: cover;
   border-radius: 5px;
   box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.65);
}
